import React, { useState, useEffect } from "react";
import "./AddMachinery.css";
import Input from "../../components/inputComponents/Input";
import Paper from "../../components/whiteCard/whiteCard";
import ContainerHeader from "../../components/containerHeader/ContainerHeader";
import Button from "../../components/inputComponents/Button";
import EditDelete from "../../components/editDelete/EditDelete";
import Axios from "axios";
import Modal from "../../components/ui/modal/Modal";
import ConfirmContent from "../../components/ui/confirmContent/ConfirmContent";
import Loader from "../../components/spinner/Spinner";
import MaterialTables from "../../components/materialTable/MaterialTables";
import url from "../../constants/url";
import Alert from "@material-ui/lab/Alert";

const AddMachinery = () => {
  const [tableContents, setTableContents] = useState([]);
  var [isSubmit, setSubmit] = useState(false);
  const [show, setShow] = useState(false);
  const [functionToPass, setFunctionToPass] = useState(null);
  const [ques, setQues] = useState("");
  const [loading, setLoading] = useState(false);
  const [update, setUpdate] = useState(true);
  const [formErrors,setFormErrors] = useState([])
  const [error, setError] = useState(false);

  const headings = [
    "SL No",
    "Description of Machinery",
    "Uploaded Details",
    "Actions",
  ];

  const [edit, setEdit] = useState(false);
  useEffect(() => {
    setLoading(true);
    setShow(true);
    Axios.get(url+"/machinery/getMachineryList")
      .then((response) => {
        let apiData = [];
        let contents = [];
        apiData = response.data;
        let i = 1;
        apiData.map((data) => {
          let tempObj = {};
          tempObj["SL No"] = i;
          i = i + 1;
          tempObj["Description of Machinery"] = data["description"];
          tempObj["Uploaded Details"] =
            data["details"] === "no-details" ? (
              data["details"]
            ) : (
              <a href={`${url}/machinery/${data["details"]}`} target="_blank">
                View Details
              </a>
            );
          tempObj["Actions"] = (
            <EditDelete
              deleteHandler={() => {
                modalVisibleForDelete(data["_id"]);
              }}
              editHandler={() => {
                editMachinery(data);
              }}
            ></EditDelete>
          );
          contents = [...contents, tempObj];
        });
        setTableContents(contents);
        modalClose();
        setLoading(false);
      })
      .catch((err) => {
        setError(true);
        setLoading(false);
      });
  }, [update]);

  const modalVisibleForEdit = () => {
    setError(false);
    let errors = []
    let toCheck = {}
    toCheck = {
      description: "DESCRIPTION OF THE MACHINARY",
    };
    Object.keys(toCheck).map((key) => {
      if (!formData[key]) {
        errors.push(toCheck[key] + " is required");
      }
    });
    if (errors.length > 0) {
      setFormErrors(errors);
      return;
    }
    setFormErrors([]);
    setFunctionToPass(edit ? () => () => editForm() : () => () => submitForm());
    setQues(
      edit
        ? "Do you want to edit this machinery?"
        : "Do you want to submit this machinery?"
    );
    setShow(true);
  };

  const modalVisibleForDelete = (id) => {
    setError(false);
    setFunctionToPass(() => () => deleteMachinery(id));
    setQues("Do you want to delete this machinery?");
    setShow(true);
  };

  const modalClose = () => {
    setShow(false);
  };

  //delete a machine
  function deleteMachinery(id) {
    setLoading(true);
    Axios.delete(url + `/machinery/deleteMachinery/${id}`)
      .then((_) => {
        modalClose();
        setLoading(false);
        setFormData({
          description: "",
          details: "",
          id: "",
        });
        setUpdate(!update);
      })
      .catch((err) => {
        setError(true);
        setLoading(false);
      });
  }

  //edit a machine
  function editMachinery(data) {
    setEdit(true);
    setFormData({
      description: data["description"],
      details: data["details"],
      id: data["_id"],
    });
  }

  const [formData, setFormData] = useState({
    description: "",
    details: "no_details.jpg",
    id: "",
  });

  //Add Machinery
  function submitForm() {
    setLoading(true);
    const data = new FormData();
    data.append("description", formData.description);
    data.append("file", formData.details);
    Axios.post(url + "/machinery/createMachinery", data)
      .then((result) => {
        modalClose();
        setSubmit(true);
        setLoading(false);
        setFormData({
          description: "",
          details: "",
          id: "",
        });

        setUpdate(!update);
      })
      .catch((err) => {
        setError(true);
        setLoading(false);
      });
  }

  //Edit Machinery
  function editForm() {
    setLoading(true);
    const data = new FormData();
    data.append("description", formData.description);
    if (formData.details) {
      data.append("file", formData.details);
    }
    Axios.patch(url + `/machinery/editMachinery/${formData.id}`, data)
      .then((result) => {
        modalClose();
        setLoading(false);
        setFormData({
          description: "",
          details: "",
          id: "",
        });
        setSubmit(true);
        setUpdate(!update);
        setEdit(!edit);
      })
      .catch((err) => {
        setError(true);
        setLoading(false);
      });
  }

  //handle change
  function handleChange(event) {
    const { name, value } = event.target;
    setFormData((prev) => {
      return {
        ...prev,
        [name]: value,
      };
    });
  }
  return (
    <div id="add-machinery">
      <Modal show={show} noHandler={modalClose}>
      {!loading ? (
          !error ? (
            <ConfirmContent
              yesHandler={functionToPass}
              noHandler={modalClose}
              question={ques}
            ></ConfirmContent>
          ) : (
            <p>Something went wrong!</p>
          )
        ) : (
          <Loader></Loader>
        )}
      </Modal>
      <ContainerHeader formName="Add Machinery"> </ContainerHeader>
      <Paper>
        <div className="add-machinery-form">
          <div className="fields">
            <div className="field">
              <Input
                name="description"
                label="Description of the Machinery *"
                size="wide"
                onChange={handleChange}
                value={formData.description}
              />
            </div>
            <div className="field">
              <Input
                type="file"
                label="Upload Details"
                name="details"
                size="wide"
                onChange={(event) => {
                  setSubmit(false);
                  const file = event.target.files[0];
                  setFormData((prev) => {
                    return {
                      ...prev,
                      details: file,
                    };
                  });
                }}
                value={isSubmit ? "" : null}
              />
            </div>
          </div>
          {formErrors.map((error) => {
            return <Alert severity="info">{error}</Alert>;
          })}
          <br />
          <hr />
          {edit ? (
            <Button
              className="primary"
              type="submit"
              label="Edit"
              onClick={modalVisibleForEdit}
            />
          ) : (
            <Button
              className="success"
              type="submit"
              label="Submit"
              onClick={modalVisibleForEdit}
            />
          )}
        </div>
        <br />
        <br />
        <MaterialTables
          headings={headings}
          data={tableContents}
          title="Machinery List"
        />
      </Paper>
    </div>
  );
};

export default AddMachinery;
