import React from 'react';
import { Page, Text, View, Document, StyleSheet, Image} from '@react-pdf/renderer';
import letterHead from '../../assets/letterHead.png'



    const styles = StyleSheet.create({
        body: {
          paddingBottom: 30,
          paddingHorizontal: 35,
          // justifyContent: 'center',
          flexDirection: 'column',
        },
        image: {
          justifyContent: 'center'
        },
        docName: {
          marginTop: 30,
          fontSize: 14,
          textAlign: 'center',
            fontFamily: 'Courier-Bold',
          textDecoration: 'underline',  
          marginBottom: 15,
        },
        section: {
          paddingTop: 20,
          flexDirection: 'row',
          justifyContent:'space-between',
          paddingBottom: 40,
        },
        sectionText: {
          fontSize: 14,
            fontFamily: 'Courier',
        },
        docNum: {
          fontSize: 12,
          textAlign: 'left',
            fontFamily: 'Courier'
        },   
        textGreeting: {
          fontSize: 14,
          textAlign: 'justify',
          fontFamily: 'Courier',
          marginBottom:20,
        },
        textContent: {
          fontSize: 13,
          textAlign: 'justify',
          fontFamily: 'Courier',
          marginBottom: 20,
        },
        textContent2: {
            fontSize: 13,
            textAlign: 'justify',
            fontFamily: 'Courier',
            
          },
        textNot: {

          fontSize: 13,
          textAlign: 'justify',
          fontFamily: 'Courier-Bold',
          marginBottom: 25,
          },	
        textJob: {
          fontSize: 13,
          textAlign: 'justify',
          fontFamily: 'Courier-Bold',
          marginBottom: 10,
        },
        sectionTextSmall: {
          fontSize: 10.25,
            fontFamily: 'Courier-Bold',
        },
        sectionTextBigg: {
          fontSize: 10.25,
            fontFamily: 'Courier-Bold',
            paddingTop: '5vh'
        },
        section2: {
          paddingTop: 40,
          flexDirection: 'row',
          justifyContent:'center',
          paddingBottom: 50,
        },
        subSection: {
          paddingLeft: 40,
          justifyContent: 'center', 
          paddingRight: 50,
          alignItems: 'center'
        },
        subSectionRight: {
          paddingLeft: 40,
          justifyContent: 'center', 
          paddingRight: 50,
          alignItems: 'right'
        },
        sign: {
          fontSize: 10.25,
          justifyContent: 'center',
        },
        nameDesignation: {
          fontSize: 14,
            fontFamily: 'Courier-Bold',
          textDecoration: 'underline',
          paddingTop: 10,
        },
        designation: {
          marginTop: 10,
          fontSize:11
        },
        textContent3: {
          fontSize: 13,
          textAlign: 'justify',
          fontFamily: 'Courier',
          marginBottom: 15
        }
        
    }) 
    const formattedDate = (dateObj) => {
      const date = new Date(dateObj);
      const year = date.getFullYear();
      let month = date.getMonth() + 1;
      let dt = date.getDate();
  
      if (dt < 10) {
        dt = "0" + dt;
      }
      if (month < 10) {
        month = "0" + month;
      }
      return dt + "/" + month + "/" + year;
    };
    const WorkOrderPrint = (props) => ( 
    <Document>
      <Page size="A4" style={styles.body} wrap>
        <Image style={styles.image} src={letterHead} />
        { props.docName === "Work Order" ? ( 
          <Text style={styles.docName}>{props.docName}</Text>
        ) : null}
        
        
        { props.docName === "Work Order" ? (
          <View style={styles.section}>
            <Text style={styles.sectionText}>Work Order No. :  {props.projectDetails.workOrder.workOrderNumber}</Text>
            <Text style={styles.sectionText}>Date: {formattedDate(props.projectDetails.workOrder.workOrderDate)} </Text>
            </View>
          ) : props.docName === "Show Cause Notice" ? (
            <View style={styles.section}>
            <Text style={styles.sectionText}>Ref No. : {props.projectDetails.prNumber}</Text>
            <Text style={styles.sectionText}>Date: {props.projectDetails.date}</Text>
            </View>
          ) : null }
          
        
        <Text style={styles.textContent2}>To,</Text>
        <Text style={styles.textContent2}>Vendor Name: {props.projectDetails.vendorName} </Text>
        <Text style={styles.textContent2}>Vendor Code: {props.projectDetails.vendorCode} </Text>
        { props.docName === "Work Order" ? (
          <Text style={styles.textContent3}>Zone : {props.projectDetails.zoneCode}</Text>
        ) : null}
        { props.docName === "Show Cause Notice" ? ( 
          <Text style={styles.docName}>{props.docName}</Text>
        ) : null}
        
        {props.docName === "Work Order" ? (
          <>
        <Text style={styles.textGreeting}>
          Dear Sir/Madam,
        </Text>
        <Text style={styles.textContent}>You are hereby advised to execute the work as mentioned below:</Text>
        <Text style={styles.textNot}>Notification No. : {props.projectDetails.notificationNum}</Text>
        <Text style={styles.textJob}>Job Description:</Text> 
        <Text style={styles.textContent}> {props.projectDetails.workOrder.workContent} </Text>
        
        <View style={styles.section}>
            <Text style={styles.sectionTextSmall}>Date of commencement of work	: { formattedDate(props.projectDetails.dos)} </Text>
          <Text style={styles.sectionTextSmall}>Expected Date of completion : {formattedDate(props.projectDetails.doc)} </Text>
        </View>
        
        <View style={styles.section2}>
                <View style={styles.subSection}>
                  <Text style={styles.sectionTextSmall}>Signature</Text>
                  <Text style={styles.nameDesignation}>{props.projectDetails.issuerName}</Text>
                  <Text style={styles.designation}>{props.projectDetails.designation}</Text>
                </View>
          
                <View style={styles.subSection}>
                    <Text style={styles.sectionTextSmall}>Signature</Text>
                    <Text style={styles.nameDesignation}>{props.projectDetails.vendor}</Text>
                <Text style={styles.designation}>Vendor code: {props.projectDetails.vendorCode} </Text>
            </View>
        </View>
          </>
        ) : props.docName === "Show Cause Notice" ? (
          <>
          <Text style={styles.textNot}>Name of Work : {props.projectDetails.projectType}</Text>
          <Text style={styles.textNot}>Notification No. : {props.projectDetails.notificationNo}</Text>
          <Text style={styles.textGreeting}>
          Dear Sir/Madam, 
        </Text>
          <Text style={styles.textContent}>
              {props.projectDetails.showCauseData}
          </Text>
          <View style={styles.subSectionRight}>
            <Text style={styles.sectionTextBigg}>Yours Faithfully</Text>
            <Text style={styles.sectionTextSmall}></Text>
            <Text style={styles.designation}>{props.projectDetails.name}</Text>
            <Text style={styles.designation}>Engineer</Text>
            <Text style={styles.designation}> Oil India Limited</Text>
          </View>
        </>
        ) : null}
        
      </Page>
    </Document>

)

 
    export default WorkOrderPrint
    