import React, {useState, useEffect} from 'react'
import * as requests from '../../../api/requests'
import ContainerHeader from '../../../components/containerHeader/ContainerHeader';
import Paper from "../../../components/whiteCard/whiteCard";
import MaterialTables from "../../../components/materialTable/MaterialTables";
import Modal from "../../../components/ui/modal/Modal";
import Loader from "../../../components/spinner/Spinner";

const AllProjects = () => {
    const [tableContents, setTableContents] = useState([]);
    const [show, setShow] = useState(false);
    const [loading, setLoading] = useState(false);
    const [error, setError] = useState(false);
    const headings =[
        "area",
        "description",
        "doc",
        "dos",
        "drawing",
        "estimate",
        "notificationNo",
        "poNumber",
        "prNumber",
        "projectId",
        "projectType",
        "showcause",
        "showcauseDate",
        "state",
        "subcontractorName",
        "subcontractorNumber",
        "technician",
        "validityLLC",
        "vendor",
        "Jr. Engineer",
        "zone"
    ]
    useEffect(()=>{
        setLoading(true);
        setShow(true);
        requests
        .get({ url: "/projects/getProjectList" })
        .then((response) => {
            let tempArr = [];
            response.map((item) => {
                let obj = {
                    area:item.area.areaName,
                    description:item.description.length > 100
                    ? item.description.slice(0, 100)
                    : item.description,
                    doc:new  Date(item.doc).toDateString(),
                    dos:new  Date(item.dos).toDateString(),
                    drawing:item.drawing,
                    estimate:item.estimate,
                    notificationNo:item.notificationNo,
                    poNumber:item.poNumber,
                    prNumber:item.prNumber,
                    projectId:item.projectId,
                    projectType:item.projectType,
                    showcause:(item.showcause)?"Issued":"Not Issued",
                    showcauseDate:(item.showcause)?item.showcauseDate:"-",
                    state:item.state,
                    subcontractorName:(item.subcontractorName)?item.subcontractorName:"-",
                    subcontractorNumber:(item.subcontractorNumber)?item.subcontractorNumber:"-",
                    technician:item.technician.name,
                    validityLLC:new  Date(item.validityLLC).toDateString(),
                    vendor:item.vendor.name,
                    "Jr. Engineer":item.jrEngineer.name,
                    zone:item.zone.zoneName
                }
                tempArr = [...tempArr, obj];
            })  
            setTableContents(tempArr);
            modalClose();
            setLoading(false);
        }).catch((e)=>{
            setError(true);
            setLoading(false);
        });
    },[]);

    const modalClose = () => {
        setShow(false);
      };
    

    return (
        <div className="vendorList">
            {loading || error ? (
                <Modal show={show} noHandler={modalClose}>
                {error ? <p>Something went wrong!</p> : <Loader></Loader>}
                </Modal>
            ) : null}
             <ContainerHeader formName="All Projects"></ContainerHeader>
            <Paper>
                <MaterialTables title="Project List" data={tableContents} headings={headings} />
            </Paper>
        </div>
    )
}

export default AllProjects
