import React, {useState} from 'react'
import ContainerHeader from "../../../components/containerHeader/ContainerHeader";
import Paper from "../../../components/whiteCard/whiteCard";
import * as requests from "../../../api/requests";
import MaterialTables from "../../../components/materialTable/MaterialTables";
import Modal from "../../../components/ui/modal/Modal";
import Loader from "../../../components/spinner/Spinner";
import DropDown from '../../../components/inputComponents/DropDown';
import Button from '../../../components/inputComponents/Button';

const CumulativeMaterialJob = () => {
    const [tableContents, setTableContents] = React.useState(null);
    const [show, setShow] = useState(false);
    const [loading, setLoading] = useState(false);
    const [error, setError] = useState(false);
    const [projectId, setProjectId] = useState("")
    const [options, setOptions] = useState([])
    const headings = [
        "SL No",
        "Description of Material",
        "Material Code",
        "Unit",
        "Cumulative Issued",
        "Cumulative Consumed",
        "Difference",
      ];
      const modalClose = () => {
        setShow(false);
      };
      React.useEffect(() => {
        setLoading(true);
        setShow(true);
        let tempOpArray = []  
        requests.get({url: "/projects/getProjectList"})
          .then(result => {
              tempOpArray = [{
                _id: "Select Project ID",
                name: "Select Project ID"
            }]
              result.forEach(project => {
                let tempOp = {}
                tempOp["_id"] = project["_id"]
                tempOp["name"] = project["projectId"]
                tempOpArray = [...tempOpArray, tempOp]
              });
              console.log(tempOpArray)
              setOptions(tempOpArray)
              modalClose();
              setLoading(false);  
          }).catch(error => {
            setError(true);
            setLoading(false);
          })
      }, [])
      const submitHandler = () => {
        setLoading(true);
        setShow(true);
        requests
            .get({url: "/projects/projectDetails?projectId=" + projectId})
            .then((result) => {
            console.log(result)
                let contents =[]
                let i = 1
                result.materials.forEach(element => {
                    let material = element.material                    
                    let tempObj = {}
                    tempObj["SL No"] = i
                    i = i + 1
                    tempObj["Description of Material"] = material.description
                    tempObj["Material Code"] = material.code
                    tempObj["Unit"] = material.unit
                    tempObj["Cumulative Issued"] = 0
                    tempObj["Cumulative Consumed"] = 0
                    console.log(tempObj)
                    contents = [...contents, tempObj];
                    if(result.detailMaterials){
                        result.detailMaterials.forEach(p => { 
                            contents.forEach(content => {
                                if(p.material.description === content["Description of Material"]){
                                    content["Cumulative Issued"] = content["Cumulative Issued"] + p.quantity
                                }
                            })                                    
                        })
                    }
                    if(result.progress){
                        result.progress.perDayProgress.forEach(p => {
                            p.materialUsed.forEach(material => {
                                contents.forEach(content => {
                                    if(material.name === content["Description of Material"]){
                                        content["Cumulative Consumed"] = content["Cumulative Consumed"] + material.consumed
                                    }
                                })
                                contents.forEach(content => {
                                    if(content["Cumulative Issued"] > content["Cumulative Consumed"]){
                                        content["Difference"] = content["Cumulative Issued"] - content["Cumulative Consumed"]
                                    }
                                })
                            })
                        });
                    }
                });
                setTableContents(contents)
                modalClose();
                setLoading(false); 
            }).catch(error => {
                setError(true);
                setLoading(false); 
            })  
      }
    return (
        <>
            {loading || error ? (
            <Modal show={show} noHandler={modalClose}>
            {error ? <p>Something went wrong!</p> : <Loader></Loader>}
            </Modal>
             ) : null}
            <Paper>
                <DropDown
                label="Project ID"
                placeholder="Enter Project ID"
                size="wide"
                name="project-id"
                options={options}
                onChange={e => setProjectId(e.target.value)}
                />
                <div style={{ marginBottom: "20px" }}>
                    <Button 
                    label="View Detailed cumulative Material List"
                    className="success"
                    onClick={submitHandler} 
                    />
                </div>
                <ContainerHeader formName="Cumulative Issued - Cumulative Consumed (Total)"/>
            {tableContents && tableContents.length ? (
                <MaterialTables
                headings={headings}
                data={tableContents}
                title="Cumulative Material Job-Wise"
            />
            ) : (
                "Please Enter a Valid Project ID"
            )}
            
            </Paper>
        </>
    )
}

export default CumulativeMaterialJob
