import React, {useState, useEffect} from 'react'
import axios from 'axios'
import ContainerHeader from '../../../components/containerHeader/ContainerHeader';
import Paper from "../../../components/whiteCard/whiteCard";
import MaterialTables from "../../../components/materialTable/MaterialTables";
import Modal from "../../../components/ui/modal/Modal";
import Loader from "../../../components/spinner/Spinner";
import DropDown from "../../../components/inputComponents/DropDown"
import url from '../../../constants/url'

const JobsInst = () => {
    const [tableContents, setTableContents] = React.useState([]);
    const [show, setShow] = useState(false);
    const [loading, setLoading] = useState(false);
    const [changed,setChanged] = useState("");
    const [error, setError] = useState(false);
    const headings = [
        "Installation",
        "area",
        "description",
        "doc",
        "dos",
        "drawing",
        "estimate",
        "notificationNo",
        "poNumber",
        "prNumber",
        "projectId",
        "projectType",
        "showcause",
        "showcauseDate",
        "state",
        "subcontractorName",
        "subcontractorNumber",
        "technician",
        "validityLLC",
        "vendor",
        "zone"
      ];
      const [areaOp, setAreaOp] = useState([
        {
          _id: null,
          name: "Select"
        }
      ]);


      useEffect(()=>{
        setLoading(true);
        setShow(true);
        let installation = [{
            _id: null,
            name: "Select"
        }]
        axios.get(url+'/installations/allInstallations')
        .then((res)=>{
            (res.data).map((item)=>{
                installation.push({
                    _id: item.name,
                    name: item.name
                })
            })
            setAreaOp(installation)
            modalClose();
            setLoading(false);
        })
        .catch((e)=>{
          setError(true);
          setLoading(false);
        })
        setTableContents(null)
    },[])

    function handleChange(e){
      setChanged(e.target.value)
  }

  useEffect(()=>{
    setLoading(true);
    setShow(true);
    axios.get(url+"/projects/getProjectList")
    .then((response) => {
        let tempArr = [];
        (response.data).map((item) => {
          if(item.installation.name === changed){
              let obj = {
                  Installation:item.installation.name,
                  area:item.area.areaName,
                  description:item.description.length > 100
                  ? item.description.slice(0, 100)
                  : item.description,
                  doc:new  Date(item.doc).toDateString(),
                  dos:new  Date(item.dos).toDateString(),
                  drawing:item.drawing,
                  estimate:item.estimate,
                  notificationNo:item.notificationNo,
                  poNumber:item.poNumber,
                  prNumber:item.prNumber,
                  projectId:item.projectId,
                  projectType:item.projectType,
                  showcause:(item.showcause)?"Issued":"Not Issued",
                  showcauseDate:(item.showcause)?item.showcauseDate:"-",
                  state:item.state,
                  subcontractorName:(item.subcontractorName)?item.subcontractorName:"-",
                  subcontractorNumber:(item.subcontractorNumber)?item.subcontractorNumber:"-",
                  technician:item.technician.name,
                  validityLLC:new  Date(item.validityLLC).toDateString(),
                  vendor:item.vendor.name,
                  zone:item.zone.zoneName
              }
              tempArr = [...tempArr, obj];
          }
      })  
        setTableContents(tempArr);
        modalClose();
        setLoading(false);
    }).catch((e)=>{
      setError(true);
      setLoading(false);
    });

},[changed])


const modalClose = () => {
  setShow(false);
};

    return (

          <div className="vendorList">
            {loading || error ? (
              <Modal show={show} noHandler={modalClose}>
                {error ? <p>Something went wrong!</p> : <Loader></Loader>}
              </Modal>
            ) : null}
            <ContainerHeader formName="Installation Wise Jobs"></ContainerHeader>
            <Paper>
            <div className="JEselect">
              <DropDown
                label="Installation*"
                name="area"
                options={areaOp}
                size="wide"
                onChange={handleChange}
              />
            </div>
            {tableContents?(<MaterialTables title="Installation Wise Jobs" data={tableContents} headings={headings} /> ):"Please Select an Installation"}
                
            </Paper>
        </div>
    )
}

export default JobsInst
