import React from 'react'
import './Navbar.css'
import menu from '../../assets/icons/menu.svg'
import dashboard from '../../assets/icons/dashboard.svg'
import user from '../../assets/icons/user.svg'
import users from '../../assets/icons/users.svg'
import vendor from '../../assets/icons/vendor.svg'
import project from '../../assets/icons/project.svg'
import material from '../../assets/icons/material.svg'
import vendor1 from '../../assets/icons/vendor1.svg'
import installation from '../../assets/icons/installation.svg'
import machinery from '../../assets/icons/machinery.svg'
import logoIcon from '../../assets/logo/logoicon.png'
import logoText from '../../assets/logo/logotext.png'
import Button from '../inputComponents/Button'
import document from '../../assets/icons/document.svg'
import {Link} from 'react-router-dom'
import * as requests from '../../api/requests'

const Navbar = (props) =>{
    const [openNav,setOpenNav] = React.useState(true)
    const [refresh,setRefresh] = React.useState(false)
    const sapNavElements = [
        {path:"/dashboard",fieldName:"Dashboard",icon:document},
        {path:"/user",fieldName:"Add User",icon:user},
        {path:"/users",fieldName:"User List",icon:users},
        {path:"/vendor",fieldName:"Add Vendor",icon:vendor1},
        {path:"/vendor-list",fieldName:"Vendor List",icon:vendor},
        {path:"/addArea",fieldName:"Add Area",icon:dashboard},
        {path:"/installation",fieldName:"Installation view and add",icon:installation},
        {path:"/add-material",fieldName:"Material view and add",icon:material},
        {path:"/add-machinery",fieldName:"Machinery view and add",icon:machinery},
        {path:"/reports",fieldName:"Reports",icon:dashboard},
        {path:"/techDocs",fieldName:"Technical Documents",icon:document}
    ]
    const gmdgmNavElements = [
        {path:"/dashboard",fieldName:"Dashboard",icon:document},
        {path:"/projects",fieldName:"Project List",icon:user},
        {path:"/notifications",fieldName:"Notification List",icon:user},
        {path:"/notification",fieldName:"Add notification",icon:user},
        {path:"/changePass", fieldName:"Change Password", icon:user},
        {path:"/techDocs",fieldName:"Technical Documents",icon:document}
    ]
    const jeNavElements = [
        {path:"/dashboard",fieldName:"Dashboard",icon:document},
        {path:"/projects",fieldName:"Project List",icon:user},
        {path:"/notification",fieldName:"Add notification",icon:user},
        {path:"/changePass", fieldName:"Change Password", icon:user},
        {path:"/techDocs",fieldName:"Technical Documents",icon:document}
    ]
    const engineerNavElements = [
        {path:"/dashboard",fieldName:"Dashboard",icon:document},
        {path:"/projects",fieldName:"Project List",icon:user},
        {path:"/notifications",fieldName:"Notification List",icon:user},
        {path:"/notification",fieldName:"Add notification",icon:user},
        {path:"/changePass", fieldName:"Change Password", icon:user},
        {path:"/techDocs",fieldName:"Technical Documents",icon:document}
    ]
    const technicianNavElements = [
        {path:"/dashboard",fieldName:"Dashboard",icon:document},
        {path:"/projects",fieldName:"Project List",icon:user},
        {path:"/notification",fieldName:"Add notification",icon:user},
        {path:"/changePass", fieldName:"Change Password", icon:user},
        {path:"/techDocs",fieldName:"Technical Documents",icon:document}
    ]

    let navElements
    switch (localStorage.getItem("Role")) {
        case "SAP":
            navElements = sapNavElements
            break;
        case "GM/DGM":
            navElements = gmdgmNavElements
            break;
        case "Engineer":
            navElements = engineerNavElements
            break;
        case "Jr. Engineer":
            navElements = jeNavElements
            break;
        case "Technician":
            navElements = technicianNavElements
            break;
        default:
            break;
    }
    const logout = () =>{
        requests.get({url:"/users/logout"}).then(data=>{
            localStorage.clear()
            window.location.pathname = "/login"
        }).catch(e=>{
        })
    }
    return(
        <div id="navbar" style={{gridTemplateColumns:openNav?"240px calc(100% - 240px)":window.innerWidth>800?"60px calc(100% - 60px)":"0px calc(100% - 0px)"}}>
            <div>
                <div className="nav-logo-container">
                    <div id="nav-logo">
                        <img src={logoIcon} alt="logo" height="35px"/>
                        {openNav?<img src={logoText} alt="logo" height="35px"/>:null}
                    </div>
                </div>
            </div>
            <div>
                <div id="top-nav">
                    <img src={menu} alt="hamburger" onClick={()=>setOpenNav(!openNav)}/>
                    <div className="button" style={{right:openNav?"240px":"60px"}}>
                        <Button label="Logout" onClick={logout}></Button>
                    </div>
                </div>
            </div>
            <div>
                <div className="side-nav-container">
                    <div id="side-nav">
                        {navElements.map(el=>{
                            return(
                                <Link to={el.path} onClick={()=>setRefresh(!refresh)} key={el.path}>
                                    <div className="nav-element" key={el.fieldName} style={{backgroundColor:(el.path.localeCompare(window.location.pathname)?"#2e2e2e":"#000")}}>
                                        <div className="nav-element-content">
                                            <p><img src={el.icon} alt="dashboard icon" height="16px"/> {openNav?el.fieldName:null}</p>
                                        </div>
                                    </div>
                                </Link>
                            )
                        })}
                    </div>
                </div>
            </div>
            <div id="body">
                {props.children}
            </div>
        </div>
    )
}

export default Navbar