import React, { Fragment, useState, useEffect } from "react";
import './ShowCause.css'
import WhiteCard from '../../../components/whiteCard/whiteCard'
import Logo from '../../../images/logo-full.png'
import axios from 'axios'
import Button from '../../../components/inputComponents/Button'
import url from '../../../constants/url'
import TextArea from "../../../components/inputComponents/TextArea";
import WorkOrderPrint from "../../../components/workOrderPrint/WorkOrderPrint";
import { PDFViewer } from "@react-pdf/renderer";
import { Redirect } from 'react-router-dom';

const ShowCause = (props) => {
    const [details,setDetails]=useState({})
    const [showPdf, setshowPdf] = useState(false);
    const [value,setValue]=useState('')
    const [ifTrue,setIfTrue] =useState(false)
    const [redirect, setRedirect] = useState(false)
    useEffect(() => {
       axios.get(url+'/projects/projectDetails?projectId='+props.match.params.id)
       .then(res=>{
           let obj = {
            vendorCode:res.data.vendor?res.data.vendor.code:"none",
            vendorName:res.data.vendor?res.data.vendor.name:"none",
            date: res.data.showcauseDate?res.data.showcauseDate:new Date().toDateString(),
            notificationNo:res.data.notificationNo?res.data.notificationNo:"none",
            prNumber:res.data.prNumber?res.data.prNumber:"none",
            projectType:res.data.projectType?res.data.projectType:"none",
            name:res.data.engineer?res.data.engineer.name:"none",
            showcause:res.data.showcause,
            showcauseDate:res.data.showcauseDate,
            showCauseData:res.data.showcauseData?res.data.showcauseData:"None"
        }
        setDetails(obj)
        setIfTrue(res.data.showcause)
       }).catch((e)=>{})
    },[])

    function submitShowCause(){
        axios.patch(url+"/projects/showCause/"+props.match.params.id,
        {
            showcause:true,
            showcauseDate:new Date().toDateString(),
            showcauseData:value
        }).then((res)=>{
           alert("saved!")
           setRedirect(true);
        }).catch((e)=>{}) 
    }

    function printDiv() { 
        setshowPdf(true)
    }

    function editShowCause(){
        setIfTrue(false);
        setValue("");
    }

    return (
        <div>
            {redirect?(<Redirect to="/projects"/>):null}
            {showPdf ? (
                 <WhiteCard>
                 <Fragment>
                   <PDFViewer height="800" width="1200" className="app">
                     <WorkOrderPrint projectDetails={details} docName="Show Cause Notice" />
                   </PDFViewer>
                 </Fragment>
               </WhiteCard>
            ):(
            <WhiteCard>
                <div id="" className="showcause">
                    <div className="sc-1">
                        <img className="sc-lh" src={Logo}/>
                        <img className="sc-lh" src={Logo}/>
                    </div>
                    <div className="sc-3"></div>
                    <div className="sc-1">
                        <span>Ref No : {details.prNumber}</span>
                        <span>Date: {details.date}</span>
                    </div>
                    <div className="sc-1">
                        <div className="sc-col">
                            <span>Vendor Name : {details.vendorName}</span>
                            <span>Vendor Code : {details.vendorCode}</span>
                        </div>
                    </div>
                    <h3 className="sc-2">Sub: Show Cause Notice</h3>
                    <div className="sc-1">
                        <div className="sc-col">
                            <span>Name of Work : {details.projectType}</span>
                            <span>Notification No. / Contract No. : {details.notificationNo}</span>
                        </div>
                    </div>
                    <div className="sc-1">
                    <div className="an-row-in">
                            Dear Sir/Madam, <br /><br />
                            <TextArea
                                value={ifTrue?details.showCauseData:value}
                                disabled={ifTrue?"true":null}
                                size="wide"
                                label="Enter Details Here:"
                                onChange={(e)=>setValue(e.target.value)}
                            />
                        </div>
                    </div>
                    <div className="sc-5">
                    <br/><br/> 
                    Yours faithfully
                    <br/><br/> <br/><br/>
                    <p>{details.name}</p>
                    <p>Engineer</p>
                    <p>Oil India Limited</p>
                    </div>
                </div>
               
                <div className="sc-button">
                    {ifTrue?
                    (<div><Button className="success" label="Print Show Cause Notice" onClick={printDiv}/><Button className="success" label="Edit Show Cause" onClick={editShowCause}/></div>):
                    (<Button className="success" label="Issue Show Cause Notice" onClick={submitShowCause}/>)}
                </div>
            </WhiteCard>)}
        </div>
    )
}

export default ShowCause











