import React, { useState, useEffect } from "react";
import ContainerHeader from "../../components/containerHeader/ContainerHeader";
import Paper from "../../components/whiteCard/whiteCard";
import "./NotificationList.css";
import { Link } from "react-router-dom";
import Axios from "axios";
import IconButton from "../../components/inputComponents/IconButton";
import MaterialTables from "../../components/materialTable/MaterialTables";
import Modal from "../../components/ui/modal/Modal";
import ConfirmContent from "../../components/ui/confirmContent/ConfirmContent";
import Loader from "../../components/spinner/Spinner";
import url from "../../constants/url";

const NotificationList = () => {
  const [tableContents, setTableContents] = useState([]);
  const [del, setDel] = useState(false);
  const [show, setShow] = useState(false);
  const [functionToPass, setFunctionToPass] = useState(null);
  const [ques, setQues] = useState("");
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(false);
  const headings = [
    "SL No",
    "Project Type",
    "Project ID",
    "Notification/Contract No",
    "Area",
    "Installation",
    "Uploaded Details",
    "Action",
  ];
  let areaList = [];
  JSON.parse(localStorage.getItem("areaAndZone")).map((area) => {
    areaList.push(area.area._id);
  });
  useEffect(() => {
    setLoading(true);
    setShow(true);
    Axios.post(url + "/notifications/getNotificationByArea", {
      area: areaList,
    })
      .then((response) => {
        let apiData = [];
        let contents = [];
        let installTemp = {};
        apiData = response.data;
        let i = 1;
        apiData.map((data) => {
          let tempObj = {};
          tempObj["SL No"] = i;
          i = i + 1;
          let actions = data["isJob"] ? (
            "JOB ASSIGNED"
          ) : (
            <div
              style={{ display: "grid", gridTemplateColumns: "1fr 1fr 1fr" }}
            >
              <div style={{ marginBottom: "10px" }}>
                <Link to={"/jobAssignment/" + data["_id"]}>
                  <IconButton
                    iconClass="fas fa-tasks"
                    className="success"
                  ></IconButton>
                </Link>
              </div>
              <div style={{ marginBottom: "10px" }}>
                <Link to={"/editNotification/" + data["_id"]}>
                  <IconButton
                    iconClass="far fa-edit"
                    className="success"
                  ></IconButton>
                </Link>
              </div>
              <div style={{ marginBottom: "10px" }}>
                <IconButton
                  iconClass="fas fa-trash-alt"
                  className="success"
                  onClick={() => modalVisible(data["_id"])}
                ></IconButton>
              </div>
            </div>
          );
          tempObj["Project Type"] = data["projectType"];
          tempObj["Project ID"] = data["projectId"];
          tempObj["Notification/Contract No"] = data["notificationNumber"];
          tempObj["Area"] = data["area"]["areaName"];
          tempObj["Installation"] = data["installation"]["name"];
          tempObj["Uploaded Details"] =
            data["uploadNotification"] === "no-details" ? (
              data["uploadNotification"]
            ) : (
              <a href={`${url}${data["uploadNotification"]}`} target="_blank">
                View Details
              </a>
            );
          tempObj["Action"] = actions;

          contents = [...contents, tempObj];
        });
        setTableContents(contents);
        modalClose();
        setLoading(false);
      })
      .catch((err) => {
        setError(true);
        setLoading(false);
      });
  }, [del]);

  const modalVisible = (id) => {
    setError(false);
    setFunctionToPass(() => () => deleteHandler(id));
    setQues("Do you want to delete this notification?");
    setShow(true);
  };

  const modalClose = () => {
    setShow(false);
  };

  const deleteHandler = (id) => {
    setLoading(true);
    Axios.delete(url + "/notifications/" + id)
      .then((res) => {
        setLoading(false);
        modalClose();
        setDel(!del);
      })
      .catch((err) => {
        setLoading(false);
        setError(true);
      });
  };

  return (
    <div className="notificationList">
      <Modal show={show} noHandler={modalClose}>
        {!loading ? (
          !error ? (
            <ConfirmContent
              yesHandler={functionToPass}
              noHandler={modalClose}
              question={ques}
            ></ConfirmContent>
          ) : (
            <p>Something went wrong!</p>
          )
        ) : (
          <Loader></Loader>
        )}
      </Modal>
      <ContainerHeader formName="Notifications List"></ContainerHeader>
      <Paper>
        <div class="notificationListTable">
          {/* {tableContents.length !== 0 ? (
            <Table
              contents={tableContents}
              setContents={setTableContents}
            ></Table>
          ) : (
            <BlankTable headings={headings}></BlankTable>
          )} */}
          <MaterialTables headings={headings} data={tableContents} />
        </div>
      </Paper>
    </div>
  );
};

export default NotificationList;
