import React, { useEffect, useState } from "react";
import "./AddProject.css";
import ContainerHeader from "../../components/containerHeader/ContainerHeader";
import Paper from "../../components/whiteCard/whiteCard";
import Input from "../../components/inputComponents/Input";
import Button from "../../components/inputComponents/Button";
import DropDown from "../../components/inputComponents/DropDown";
import TextArea from "../../components/inputComponents/TextArea";
import MultiInput from "../../components/inputComponents/MultiInput";
import Axios from "axios";
import Modal from "../../components/ui/modal/Modal";
import ConfirmContent from "../../components/ui/confirmContent/ConfirmContent";
import Loader from "../../components/spinner/Spinner";
import url from "../../constants/url";
import Alert from "@material-ui/lab/Alert";

const AddProject = (props) => {
  const [details, setDetails] = useState({
    projectType: "",
    area: "",
    zone: "",
    zoneName: "",
    areaName: "",
    state: "",
    installation: "",
    installationName: "",
    notificationNo: 0,
    projectId: "",
    notification: "",
    description: "",
    technician: "",
    vendor: "",
    engineer: "",
    jrEngineer: "",
    dos: "",
    doc: "",
    validityLLC: "",
    prNumber: 0,
    poNumber: 0,
    subcontractorName: "",
    subcontractorNumber: 0,
    remarks: "",
  });
  const [flag, setFlag] = useState(false);
  const [technician, setTechnician] = useState([]);
  const [je, setJe] = useState([]);
  const [eng, setEng] = useState([]);
  const [sub, setSub] = useState(false);
  const [vendor, setVendor] = useState([]);
  const [show, setShow] = useState(false);
  const [zones, setZones] = useState([]);
  const [showModal, setShowModal] = useState(false);
  const [functionToPass, setFunctionToPass] = useState(null);
  const [ques, setQues] = useState("");
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(false);
  const [formErrors, setFormErrors] = useState([]);

  const listObj = { name: "Select", _id: "select" };
  const states = [
    { name: "Select", _id: "select" },
    { name: "Completed", _id: "Completed" },
    { name: "Halt", _id: "Halt" },
    { name: "Ongoing", _id: "Ongoing" },
  ];
  useEffect(() => {
    setLoading(true);
    setShowModal(true);
    Axios.get(url + "/notifications/" + props.match.params.id)
      .then((response) => {
        let tempObj = { ...details };
        let apiObj = {};
        apiObj = response.data[0];
        tempObj["projectType"] = apiObj["projectType"];
        tempObj["areaName"] = apiObj["area"]["areaName"];
        tempObj["area"] = apiObj["area"]["_id"];
        tempObj["state"] = apiObj["state"];
        tempObj["installation"] = apiObj["installation"]["_id"];
        tempObj["installationName"] = apiObj["installation"]["name"];
        tempObj["notificationNo"] = apiObj["notificationNumber"];
        tempObj["projectId"] = apiObj["projectId"];
        tempObj["notification"] = props.match.params.id;
        tempObj["description"] = apiObj["description"];
        setDetails(tempObj);
        Axios.get(url + "/areas/" + apiObj["area"]["_id"])
          .then((response) => {
            let zone = response.data["zones"];
            zone.unshift(listObj);
            setZones(zone);
            setFlag(true);
            modalClose();
            setLoading(false);
          })
          .catch((err) => {
            setError(true);
            setLoading(false);
          });
      })
      .catch((err) => {
        setError(true);
        setLoading(false);
      });
  }, []);

  const modalVisible = () => {
    setError(false);
    let errors = [];
    let toCheck = {};
    toCheck = {
      state: "STATE",
      zone: "ZONE",
      vendor: "VENDOR",
      dos: "DOS",
      doc: "DOC",
      technician: "TECHNICIAN",
      jrEngineer: "JR. ENGINEER",
      engineer: "ENGINEER",
    };
    if (sub) {
      toCheck = {
        ...toCheck,
        subcontractorName: "Sub-coordinator/POA holder name",
        subcontractorNumber: "Sub-coordinator/POA holder No.",
      };
    }

    Object.keys(toCheck).map((key) => {
      if (!details[key]) {
        errors.push(toCheck[key] + " is required");
      }
    });
    if (errors.length > 0) {
      setFormErrors(errors);
      return;
    }
    setFormErrors([]);
    setQues("Do you want to assign this job?");
    setFunctionToPass(() => () => submitHandler());
    setShowModal(true);
  };

  const modalClose = () => {
    setShowModal(false);
  };

  const inputChangeHandler = (event, val) => {
    setDetails({
      ...details,
      [val]: event.target.value,
    });
    if (val === "zone") {
      setError(false);
      setLoading(true);
      setShowModal(true);
      Axios.post(url + "/users/findUsersInZone", {
        zoneId: event.target.value,
      })
        .then((response) => {
          let tempArr = [];
          tempArr = response.data["technician"];
          tempArr.unshift(listObj);
          setTechnician(tempArr);
          tempArr = response.data["je"];
          tempArr.unshift(listObj);
          setJe(tempArr);
          tempArr = response.data["engineer"];
          tempArr.unshift(listObj);
          setEng(tempArr);
          tempArr = response.data["vendors"];
          tempArr.unshift(listObj);
          setVendor(tempArr);
          setShow(true);
          modalClose();
          setLoading(false);
        })
        .catch((err) => {
          setError(true);
          setLoading(false);
          window.location.reload();
        });
    }
  };

  const submitHandler = () => {
    console.log(details);
    setLoading(true);
    Axios.post(url + "/projects/createProject", details)
      .then((response) => {
        modalClose();
        setLoading(false);
        props.history.push("/projects");
      })
      .catch((err) => {
        setError(true);
        setLoading(false);
      });
  };

  const subcoHandler = (event) => {
    if (event.target.value == "Yes") setSub(true);
    else setSub(false);
  };
  return (
    <div id="add-project">
      <Modal show={showModal} noHandler={modalClose}>
        {!loading ? (
          !error ? (
            <ConfirmContent
              yesHandler={functionToPass}
              noHandler={modalClose}
              question={ques}
            ></ConfirmContent>
          ) : (
            <p>Something went wrong!</p>
          )
        ) : (
          <Loader></Loader>
        )}
      </Modal>
      <ContainerHeader formName="Job Assignment"></ContainerHeader>
      {flag ? (
        <Paper>
          <div className="add-project-form">
            <div className="add-project-grid1">
              <div className="fields">
                <div className="field">
                  <Input
                    name="project-type"
                    label="Project Type *"
                    disabled={true}
                    size="wide"
                    value={details.projectType}
                  />
                </div>
                <div className="field">
                  <Input
                    name="project-id"
                    label="Project ID *"
                    disabled={true}
                    size="wide"
                    value={details.projectId}
                  />
                </div>
                <div className="field">
                  <Input
                    name="notification-no"
                    label="Notification No *"
                    disabled={true}
                    size="wide"
                    value={details.notificationNo}
                  />
                </div>
                <div className="field">
                  <Input
                    name="installation"
                    label="Installation *"
                    disabled={true}
                    size="wide"
                    value={details.installationName}
                  />
                </div>
                <div className="field">
                  <Input
                    name="area"
                    label="Area *"
                    disabled={true}
                    size="wide"
                    value={details.areaName}
                  />
                </div>
              </div>
            </div>
            <div className="fields">
              <div className="field">
                <TextArea
                  label="Description of Project *"
                  name="description"
                  size="wide"
                  disabled={true}
                  value={details.description}
                />
              </div>
            </div>
            <div className="add-project-grid2">
              <div className="fields">
                <div className="field">
                  <DropDown
                    size="wide"
                    label="State *"
                    options={states}
                    onChange={(e) => inputChangeHandler(e, "state")}
                  />
                </div>
                <div className="field">
                  <DropDown
                    size="wide"
                    label="Zone *"
                    options={zones}
                    onChange={(e) => inputChangeHandler(e, "zone")}
                  />
                </div>
                {show ? (
                  <div className="field">
                    <DropDown
                      size="wide"
                      label="Assign to Vendor *"
                      options={vendor}
                      onChange={(e) => inputChangeHandler(e, "vendor")}
                    />
                  </div>
                ) : null}
                <div className="field">
                  <MultiInput
                    label="Sub Contractor Engaged/POA"
                    name="is-engaged"
                    type="radio"
                    options={["Yes", "No"]}
                    onChange={subcoHandler}
                  />
                </div>
                {sub ? (
                  <div className="field">
                    <Input
                      name="subName"
                      label="Sub-contractor/POA holder name*"
                      size="wide"
                      value={details.subcontractorName}
                      onChange={(e) =>
                        inputChangeHandler(e, "subcontractorName")
                      }
                    />
                  </div>
                ) : null}
                {sub ? (
                  <div className="field">
                    <Input
                      name="subNo"
                      label="Sub-coordinator/POA holder No. *"
                      size="wide"
                      value={details.subcontractorNumber}
                      onChange={(e) =>
                        inputChangeHandler(e, "subcontractorNumber")
                      }
                    />
                  </div>
                ) : null}
                <div className="field">
                  <Input
                    type="date"
                    name="dos"
                    placeholder="Select Date"
                    label="DOS *"
                    size="wide"
                    onChange={(e) => inputChangeHandler(e, "dos")}
                  />
                </div>
                <div className="field">
                  <Input
                    type="date"
                    name="doc"
                    placeholder="Select Date"
                    label="DOC *"
                    size="wide"
                    onChange={(e) => inputChangeHandler(e, "doc")}
                  />
                </div>
                {show ? (
                  <div className="field">
                    <DropDown
                      size="wide"
                      label="Assign to Technician *"
                      options={technician}
                      onChange={(e) => inputChangeHandler(e, "technician")}
                    />
                  </div>
                ) : null}
                {show ? (
                  <div className="field">
                    <DropDown
                      size="wide"
                      label="Assign to Jr. Engineer *"
                      options={je}
                      onChange={(e) => inputChangeHandler(e, "jrEngineer")}
                    />
                  </div>
                ) : null}
                {show ? (
                  <div className="field">
                    <DropDown
                      size="wide"
                      label="Assign to Engineer *"
                      options={eng}
                      onChange={(e) => inputChangeHandler(e, "engineer")}
                    />
                  </div>
                ) : null}
                <div className="field">
                  <Input
                    type="date"
                    name="lcc-date"
                    placeholder="Select Date"
                    label="Validity date of LCC"
                    size="wide"
                    onChange={(e) => inputChangeHandler(e, "validityLLC")}
                  />
                </div>
              </div>
              <div className="text-area">
                <TextArea
                  label="Remarks"
                  name="remarks"
                  size="wide"
                  onChange={(e) => inputChangeHandler(e, "remarks")}
                />
              </div>
            </div>
            {formErrors.map((error) => {
              return <Alert severity="info">{error}</Alert>;
            })}
            <hr />
            <Button
              className="success"
              label="Submit"
              onClick={modalVisible}
            ></Button>
          </div>
        </Paper>
      ) : null}
    </div>
  );
};

export default AddProject;
