import React from "react";
import { Redirect } from "react-router-dom";
import Paper from "../../components/whiteCard/whiteCard";
import url from '../../constants/url'
const styles = {
  structure:{
    display:"grid",
    gridTemplateColumns:"2fr 1fr"
  }
}

const Home = () => {
  return (
    <div>
      <Paper>
        <div id="dashboard" style={styles.structure}>
          <div style={{padding:"20px",...styles.structure}}>
            <div>
            <p>Name:</p>
            <p>Mobile Number:</p>
            <p>Email:</p>
            <p>Status:</p>
            <p>User Code:</p>
            </div>
            <div>
              <p>{localStorage.getItem("Name")}</p>
              <p>{localStorage.getItem("mobile")}</p>
              <p>{localStorage.getItem("Email")}</p>
              <p>{localStorage.getItem("status")}</p>
              <p>{localStorage.getItem("userCode")}</p>
            </div>
          </div>
          <div style={{padding:"20px"}}>
            <img src={url+"/userPics/"+localStorage.getItem("photo")} alt="User Photo" width="200px"/>
          </div>
        </div>  
      </Paper>  
    </div>
  );
};

export default Home;
