import React, { useState } from "react";
import "./ProjectList.css";
import ContainerHeader from "../../components/containerHeader/ContainerHeader";
import Paper from "../../components/whiteCard/whiteCard";
import Input from "../../components/inputComponents/Input";
import IconButton from "../../components/inputComponents/IconButton";
import Button from "../../components/inputComponents/Button";
import { Link } from "react-router-dom";
import * as requests from "../../api/requests";
import MaterialTables from "../../components/materialTable/MaterialTables";
import Axios from "axios";
import Modal from "../../components/ui/modal/Modal";
import ConfirmContent from "../../components/ui/confirmContent/ConfirmContent";
import Loader from "../../components/spinner/Spinner";
import url from "../../constants/url";

const ProjectList = () => {
  const [tableContents, setTableContents] = React.useState(null);
  const [show, setShow] = useState(false);
  const [update, setUpdate] = useState(false);
  const [functionToPass, setFunctionToPass] = useState(false);
  const [loading, setLoading] = useState(false);
  const [ques, setQues] = useState("");
  const [error, setError] = useState(false);
  const headings = [
    "Project Type",
    "Project Id",
    "Project Description",
    "Zone",
    "Area",
    "PO Number",
    "PR Number",
    "Notification Number",
    "State",
    "Subcontractor",
    "Technician",
    "Vendor",
    "Upload Estimate",
    "Upload Drawing",
    "Actions",
  ];
  const getTotalProgress = (array) => {
    let progress = 0;
    array.map((day) => {
      progress = progress + day.progress;
    });
    return progress;
  };

  const uploadEstimateHandler = (file, id) => {
    setLoading(true);
    const data = new FormData();
    data.append("file", file);
    data.append("projectId", id);
    data.append("name", "pic");
    Axios.post(url + "/projects/uploadEstimate", data)
      .then((res) => {
        modalClose();
        setLoading(false);
      })
      .catch((err) => {
        setError(true);
        setLoading(false);
      });
  };

  const uploadDrawingHandler = (file, id) => {
    setLoading(true);
    const data = new FormData();
    data.append("file", file);
    data.append("projectId", id);
    data.append("name", "pic");
    Axios.post(url + "/projects/uploadDrawing", data)
      .then((res) => {
        modalClose();
        setLoading(false);
      })
      .catch((err) => {
        setError(true);
        setLoading(false);
      });
  };

  const modalVisibleForEstimate = (file, id) => {
    setError(false);
    setFunctionToPass(() => () => uploadEstimateHandler(file, id));
    setQues("Do you want to upload this estimate?");
    setShow(true);
  };

  const modalVisibleForDrawing = (file, id) => {
    setError(false);
    setFunctionToPass(() => () => uploadDrawingHandler(file, id));
    setQues("Do you want to upload this drawing?");
    setShow(true);
  };
  React.useEffect(() => {
    setLoading(true);
    setShow(true);
    if (localStorage.getItem("Role") === "GM/DGM") {
      requests
        .get({ url: "/projects/getProjectList" })
        .then((data) => {
          let tempProjectList = [];
          data.map((project) => {
            const path = "/project-details/" + project._id;
            tempProjectList.push({
              "Project Type": project.projectType,
              "Project Id": project.projectId,
              "Project Description":
                project.description.length > 100
                  ? project.description.slice(0, 100)
                  : project.description,
              Zone: project.zone ? project.zone.zoneName : "",
              Area: project.area ? project.area.areaName : "",
              "PO Number": project.poNumber,
              "PR Number": project.prNumber,
              "Notification Number": project.notificationNo,
              State:
                getTotalProgress(project.progress.perDayProgress) <= 100
                  ? "completed"
                  : project.state,
              Subcontractor: project.subcontractorName ? "Yes" : "No",
              Technician: project.technician ? project.technician.name : null,
              Vendor: project.vendor ? project.vendor.name : null,
              "Upload Estimate": (
                <div>
                  <Input
                    name="estimate"
                    label="Upload estimate"
                    size="wide"
                    type="file"
                    onChange={(event) => {
                      const file = event.target.files[0];
                    }}
                  />
                  <IconButton
                    className="success"
                    hint="Issue job completion"
                    iconClass="fa fa-check-circle-o"
                    onClick={() => uploadEstimateHandler()}
                  ></IconButton>
                </div>
              ),
              "Upload Drawing": (
                <Input type="file" label="Upload drawing"></Input>
              ),
              Actions: (
                <div className="action-buttons">
                  <Link to={path}>
                    <IconButton
                      className="success"
                      hint="Project details"
                      iconClass="fa fa-info"
                    ></IconButton>
                  </Link>
                  <Link
                    to={
                      "/daily-progress/" +
                      project._id +
                      "/" +
                      project.progress._id
                    }
                  >
                    <IconButton
                      className="success"
                      hint="Tally book"
                      iconClass="fa fa-book"
                    ></IconButton>
                  </Link>
                  <Link to="/project">
                    <IconButton
                      className="danger"
                      hint="Edit"
                      iconClass="fa fa-pencil"
                    ></IconButton>
                  </Link>
                  <Link to={"/materials/" + project._id}>
                    <IconButton
                      className="success"
                      hint="Issue material"
                      iconClass="fa fa-file-text"
                    ></IconButton>
                  </Link>
                  <Link to={"/work-order/" + project._id}>
                    <IconButton
                      className="success"
                      hint="Issue work order"
                      iconClass="fa fa-file-word-o"
                    ></IconButton>
                  </Link>
                  {localStorage.getItem("Role") === "GM/DGM" ||
                  localStorage.getItem("Role") === "Engineer" ? (
                    <Link to={"/show-cause/" + project._id}>
                      <IconButton
                        className="success"
                        hint="Issue show cause"
                        iconClass="fa fa-sticky-note-o"
                      ></IconButton>
                    </Link>
                  ) : null}
                  <Link to="/job-completion">
                    <IconButton
                      className="success"
                      hint="Issue job completion"
                      iconClass="fa fa-check-circle-o"
                    ></IconButton>
                  </Link>
                  <IconButton
                    className="danger"
                    hint="Delete"
                    iconClass="fa fa-trash"
                    onClick={() => deleteHandler(project._id)}
                  ></IconButton>
                  <IconButton
                    className="success"
                    hint="Submit"
                    iconClass="fa fa-check"
                  ></IconButton>
                </div>
              ),
            });
          });
          setTableContents(tempProjectList);
          modalClose();
          setLoading(false);
        })
        .catch((e) => {
          setError(true);
          setLoading(false);
        });
    } else {
      requests
        .post({
          url: "/projects/getUserProjects",
          body: { userId: localStorage.getItem("Id") },
        })
        .then((data) => {
          let tempProjectList = [];
          let file;
          data.map((project) => {
            const path = "/project-details/" + project._id;
            tempProjectList.push({
              "Project Type": project.projectType,
              "Project Id": project.projectId,
              "Project Description":
                project.description.toString().length > 100
                  ? project.description.slice(0, 100)
                  : project.description,
              Zone: project.zone ? project.zone.zoneName : "",
              Area: project.area ? project.area.areaName : "",
              "PO Number": project.poNumber,
              "PR Number": project.prNumber,
              "Notification Number": project.notificationNo,
              State:
                project.progress &&
                getTotalProgress(project.progress.perDayProgress) >= 100
                  ? "completed"
                  : project.state,
              Subcontractor: project.subcontractorName ? "Yes" : "No",
              Technician: project.technician ? project.technician.name : null,
              Vendor: project.vendor ? project.vendor.name : null,
              "Upload Estimate": (
                <div>
                  <Input
                    name="estimate"
                    size="wide"
                    type="file"
                    onChange={(event) => {
                      file = event.target.files[0];
                    }}
                  />
                  <Button
                    className="success"
                    hint="Upload estimate"
                    label="Upload"
                    onClick={() => modalVisibleForEstimate(file, project._id)}
                  ></Button>
                </div>
              ),
              "Upload Drawing": (
                <div>
                  <Input
                    name="drawing"
                    size="wide"
                    type="file"
                    onChange={(event) => {
                      file = event.target.files[0];
                    }}
                  />
                  <Button
                    className="success"
                    hint="Upload drawing"
                    label="Upload"
                    onClick={() => modalVisibleForDrawing(file, project._id)}
                  ></Button>
                </div>
              ),
              Actions: (
                <div className="action-buttons">
                  <Link to={path}>
                    <IconButton
                      className="success"
                      hint="Project details"
                      iconClass="fa fa-info"
                    ></IconButton>
                  </Link>
                  {project.progress && project.state !== "Removed" ? (
                    <Link
                      to={
                        "/daily-progress/" +
                        project._id +
                        "/" +
                        project.progress._id
                      }
                    >
                      <IconButton
                        className="success"
                        hint="Tally book"
                        iconClass="fa fa-book"
                      ></IconButton>
                    </Link>
                  ) : null}
                  {project.state !== "Completed" ? (
                    <>
                      <Link to={"/editProject/" + project._id}>
                        <IconButton
                          className="danger"
                          hint="Edit"
                          iconClass="fa fa-pencil"
                        ></IconButton>
                      </Link>
                      {project.progress && project.state !== "Removed" ? (
                        <Link to={"/materials/" + project._id}>
                          <IconButton
                            className="success"
                            hint="Issue material"
                            iconClass="fa fa-file-text"
                          ></IconButton>
                        </Link>
                      ) : null}
                      {project.state !== "Removed" ? (
                        <Link to={"/work-order/" + project._id}>
                          <IconButton
                            className="success"
                            hint="Issue work order"
                            iconClass="fa fa-file-word-o"
                          ></IconButton>
                        </Link>
                      ) : null}
                      {(localStorage.getItem("Role") === "GM/DGM" ||
                        localStorage.getItem("Role") === "Engineer") &&
                      project.state !== "Removed" ? (
                        <Link to={"/show-cause/" + project._id}>
                          <IconButton
                            className="success"
                            hint="Issue show cause"
                            iconClass="fa fa-sticky-note-o"
                          ></IconButton>
                        </Link>
                      ) : null}
                    </>
                  ) : null}
                  {project.state === "Completed" &&
                  project.state !== "Removed" ? (
                    <Link to="/job-completion">
                      <IconButton
                        className="success"
                        hint="Issue job completion"
                        iconClass="fa fa-check-circle-o"
                      ></IconButton>
                    </Link>
                  ) : null}
                  {project.state !== "Completed" &&
                  project.state !== "Removed" ? (
                    <IconButton
                      className="danger"
                      hint="Delete"
                      iconClass="fa fa-trash"
                      onClick={() => modalVisibleForDelete(project._id)}
                    ></IconButton>
                  ) : null}
                  {(localStorage.getItem("Role") === "GM/DGM" ||
                    localStorage.getItem("Role") === "Engineer") &&
                  project.state !== "Completed" &&
                  project.progress &&
                  project.state !== "Removed" ? (
                    <Link to={"/submitJob/" + project._id}>
                      <IconButton
                        className="success"
                        hint="Submit"
                        iconClass="fa fa-check"
                      ></IconButton>
                    </Link>
                  ) : null}
                </div>
              ),
            });
          });
          setTableContents(tempProjectList);
          modalClose();
          setLoading(false);
        })
        .catch((e) => {
          setError(true);
          setLoading(false);
        });
    }
  }, [update]);

  const modalVisibleForDelete = (id) => {
    setError(false);
    setFunctionToPass(() => () => deleteHandler(id));
    setQues("Do you want to delete this project?");
    setShow(true);
  };

  const modalClose = () => {
    setShow(false);
  };

  const deleteHandler = (id) => {
    setLoading(true);
    Axios.delete(url + "/projects/deleteProject/" + id)
      .then((resp) => {
        setLoading(false);
        modalClose();
        setUpdate(!update);
      })
      .catch((err) => {});
  };
  return (
    <div id="project-list">
      <Modal show={show} noHandler={modalClose}>
        {loading ? (
          <Loader></Loader>
        ) : !error ? (
          <ConfirmContent
            yesHandler={functionToPass}
            noHandler={modalClose}
            question={ques}
          ></ConfirmContent>
        ) : (
          <p>Something went wrong!</p>
        )}
      </Modal>
      <ContainerHeader formName="Project List"></ContainerHeader>
      <Paper>
        <div className="project-list-table">
          {tableContents && tableContents.length > 0 ? (
            <MaterialTables headings={headings} data={tableContents} />
          ) : (
            "No projects"
          )}
        </div>
      </Paper>
    </div>
  );
};

export default ProjectList;
