import React, { Fragment } from "react";
import "./Button.css";

const Button = ({ type, className, onClick, label, disabled, ...props }) => {
  return (
    <Fragment>
      <button
        type={type}
        className={className}
        onClick={onClick}
        disabled={disabled}
      >
        {label}
      </button>
    </Fragment>
  );
};

export default Button;
