import React, { useState } from "react";
import MaterialTable from "material-table";

const MaterialTables = ({ headings, data, title }) => {
  const [selectedRow, setSelectedRow] = useState(null);
  const columns = [];

  headings.map((header) => {
    if (
      header === "Action" ||
      header === "Actions" ||
      header === "Uploaded Details" ||
      header === "View Show Cause Notice"
    ) {
      columns.push({
        title: header,
        field: header,
        export: false,
      });
    } else {
      columns.push({
        title: header,
        field: header,
      });
    }
  });

  return (
    <MaterialTable
      title={title}
      columns={columns}
      data={data}
      onRowClick={(evt, selectedRow) =>
        setSelectedRow(selectedRow.tableData.id)
      }
      options={{
        headerStyle: {
          backgroundColor: "#dcd6f7",
          color: "#3e206d",
        },

        rowStyle: (rowData) => ({
          backgroundColor:
            selectedRow === rowData.tableData.id ? "#EEE" : "#FFF",
        }),

        exportButton: true,
        exportAllData: true,
      }}
    />
  );
};

MaterialTables.defaultProps = {
  title: "List",
};

export default MaterialTables;
